export class App extends React.Component{
  aryPrj = [];

  constructor(props){
    super(props);
    this.state = {
      winWidth : 0,
      winHeight : 0,
      vidWidth: 0,
      vidHeight: 0
    };

    this.updateDimensions = this.updateDimensions.bind(this);
    this.refreshVideoComponents = this.refreshVideoComponents.bind(this);
  }

  refreshVideoComponents(){
    this.aryPrj = [];

    pdata.projects.forEach( (item, i) => {
        this.aryPrj.push(<MyVid key={item.vid} vid={item.vid} opts={{height: this.state.vidHeight, width: this.state.vidWidth }} title={item.title} msg={item.msg}/>);
      }
    );
  }
  
  componentDidMount(){
    this.updateDimensions();
    window.addEventListener('resize', this.updateDimensions);

    // console.log(pdata);
  }

  componentWillUnmount(){
    window.removeEventListener('resize', this.updateDimensions);
  }

  updateDimensions(){
    let windowWidth = typeof window !== "undefined" ? window.innerWidth : 0;
    let windowHeight = typeof window !== "undefined" ? window.innerHeight : 0;

    let vidW = windowWidth * 0.75;
    let vidH = vidW * 9 / 16;

    this.setState({ winWidth: windowWidth, winHeight: windowHeight, vidWidth : vidW, vidHeight : vidH });
  }

  // prj = pdata.projdects.map( (_prj, i) =>{
  //   return (
  //     <MyVid vid={_prj.vid} opts={{height: this.state.vidHeight, width: this.state.vidWidth }} title={_prj.title} msg={_prj.msg}/>
  //   )
  // });

  slideChangeHandler = (index) => {
    console.log(index);
  };

  render() {
    this.refreshVideoComponents();
    return (
      <div className="App">
        <header className="App-header">
          <Profile name="Ho Pong, Shum"/>
          <div style={{width: '80vw', height: this.state.vidHeight * 2, backgroundColor : '#282c40', paddingTop : '15px', margin : '10px'}} >
            <Slider hasArrows="true" onSlideChange={this.slideChangeHandler}>  
              {/* <MyVid vid="lkZtfVtnYe0" opts={{height: this.state.vidHeight, width: this.state.vidWidth }} />
              <MyVid vid="QQr1bBCstYg" opts={{height: this.state.vidHeight, width: this.state.vidWidth }} />
              <MyVid vid="4mvLd6SNFpI" opts={{height: this.state.vidHeight, width: this.state.vidWidth }} />
              <MyVid vid="j9vATdn8_NU" opts={{height: this.state.vidHeight, width: this.state.vidWidth }} />
              <MyVid vid="NGNaxZBIT0E" opts={{height: this.state.vidHeight, width: this.state.vidWidth }} /> */}
              {this.aryPrj}
            </Slider>
          </div>

        </header>
      </div>
    );
  }
}

