import React from 'react';
import Youtube from 'react-youtube';

var ytObject = null;
export class MyVid extends React.Component{
    constructor(props){
        super(props);

        this.pause = this.pause.bind(this);
        this._onReady = this._onReady.bind(this);
    }

    _onReady(event){
        ytObject = event;
        this.pause();
    }

    pause(){
        ytObject.target.pauseVideo();
    }

    render(){
        return (
            <div>
                <Youtube videoId={this.props.vid} opts={this.props.opts} onReady={this._onReady} host='https://www.youtube.com'/>
                <div>
                    <h1>{this.props.title}</h1>
                    <div className="small_text" style={{ textAlign: "left" }} dangerouslySetInnerHTML={{ __html: this.props.msg}}></div>
                </div>
            </div>
        );
    }


}